.menuContaner {
    height: 35px;
    background: linear-gradient(to right, #0f733b 0%, #294aa5 80%);
    color: white;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    align-items: center;

    z-index: 999;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}

.menuContaner>a>li {
    list-style: none;
    margin-right: 8px;
    margin-left: 8px;
    cursor: pointer;
    padding: 0.2rem;
    color: white;
}

a {
    text-decoration: none;
}


@media screen and (max-width: 1080px) {

    .banner,
    .banner_contactInfo {
        display: block;
        max-width: 100%;
    }

    .banner {
        padding-bottom: 0.2rem;
        border-bottom: 1px dashed red;
    }

    .banner_contactInfo {
        max-width: 100%;
        display: flex;
        border: none;
        padding-top: 0.5rem;
        justify-content: center;
    }

    .banner_contactInfo>ul>li {
        display: inline-block;
    }



    .menuContaner {
        justify-content: center;
    }

    .menuContaner>a>li {
        margin-left: 5px;
        margin-right: 5px;
    }

    .footerContainer>div {
        flex-basis: 49%;
        margin-bottom: 3px;
    }

}

@media screen and (max-width: 580px) {

    .menuContaner>a>li {
        margin: 0px;
        border-right: 0.5px solid grey;
        border-left: 0.5px solid grey;
    }

    .footerContainer>div {
        flex-basis: 100%;
        margin-bottom: 3px;
    }

}